import React, {Fragment, useEffect, useState} from 'react';
import * as keySetting from "./keySetting";
import "./font.css";
import "./reset.css";
import "./main.css";

function App() {
    const [defaultKey, setDefaultKey] = useState('');

    useEffect(() =>{
        setDefaultKey(keySetting.defaultKey);
    }, [])

    return (
        <Fragment>
            <h2>큐브 테스트</h2>
            <div className="center_box">
                <form onSubmit={keySetting.keyTransfer}>
                    <label>API Key <input type="text" name="api_key" defaultValue={defaultKey} /></label>

                    <div className="btn_div">
                        <button type="submit" className="formBtn" id="btn1">Key 전송</button>
                    </div>
                </form>
            </div>

            <span id="total_cnt"></span>
            <div id="view_area"></div>

            <div className="footer">
                <font>Data based on NEXON DEVELOPERS</font>
                https://github.com/Memiru/Mushroom_API <br/>
                루나 / 고래성이당
            </div>
        </Fragment>
    );
}

export default App;